/* Import Google Font - Poppins */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

body {
    display: flex;
    padding: 0 10px;
    min-height: 100vh;
    align-items: center;
    background: #4CB9E7;
    justify-content: center;
}

.wrapper {
    height: 400px;
    max-width: 410px;
    background: #fff;
    border-radius: 7px;
    padding: 20px 25px 0;
    transition: height 0.2s ease;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

#error {
    color: red;
}

.wrapper.active {
    height: 530px;
}



h2 {
    color: whitesmoke;
}

.wrapper .form {
    margin: 20px 0 25px;
}

.form :where(input, button) {
    width: 100%;
    height: 55px;
    border: none;
    outline: none;
    border-radius: 5px;
    transition: 0.1s ease;
}

.form input {
    font-size: 18px;
    padding: 0 17px;
    border: 1px solid #999;
}

h1 {
    color: black;
}

p {
    color: white
}

.form input:focus {
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.13);
}

/* QRCodeGenerator.css */
/* ... (your existing styles) */

.qr-code-container {
    margin-top: 20px;
    text-align: center;
}

.form input::placeholder {
    color: #999;
}

.form button {
    color: #fff;
    cursor: pointer;
    margin-top: 20px;
    font-size: 17px;
    background: #3498DB;
}







@media (max-width: 430px) {
    .wrapper {
        height: 455px;
        padding: 16px 20px;
    }

    .wrapper.active {
        height: 510px;
    }

    header p {
        color: #696969;
    }

    .form :where(input, button) {
        height: 52px;
    }

}